*,*::after,*::before {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page__sections {
  display: flex;
  flex-direction: row-reverse;
}

.page__main {
  border: 1px solid grey;
  width: 70%;
}

.result__style {
  display: flex;
  flex-flow: wrap;
}

.result__style .swap__style {
  background-color: #242F2C;
  color: #82ABA1;
}

.result__style li {
  background-color: #82ABA1;
  color: #242F2C;
  margin: 10px;
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
}

.result__style img {
  display: inline;
}

.result__style p {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}
